import CartItem from "../CartItem/CartItem";
import { Wrapper } from "./Cart.styles";
import { CartItemType } from "../App";
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined';

type Props = {
    cartItem: CartItemType[];
    addToCart: (clickedItem: CartItemType) => void;
    removeFromCart: (id: number) => void;
    setCartOpen: any;
}
const Cart: React.FC<Props> = ({ cartItem, addToCart, removeFromCart,setCartOpen }) => {

    const calculateTotal = (item: CartItemType[]) =>item.reduce((ack:number,item)=>ack+item.amount*item.price,0)
        
    
    return (
        <Wrapper>
            <div className="header">
                <h2>Your Shopping Cart</h2>
                <button onClick={()=>setCartOpen(false)} className="cancel"><CancelOutlinedIcon color="error" fontSize="large"/></button>
            </div>
            {cartItem.length === 0 ? <p>No items in cart.</p> : null}
            {cartItem.map(item => (
                <CartItem
                    key={item.id}
                    item={item}
                    addToCart={addToCart}
                    removeFromCart={removeFromCart}
                />
            ))}
            <h2>Total: $ {calculateTotal(cartItem).toFixed(2)}</h2>
        </Wrapper>
    )
}

export default Cart;