import { useState } from "react";
import { useQuery } from "react-query";
// COMPONENTS
import Item from "./Item/Item";
import { Drawer, Grid,Badge, CircularProgress } from "@mui/material";
import { AddShoppingCart } from "@mui/icons-material";
import Cart from "./Cart/Cart";
// STYLING
import { Wrapper ,StyledButton} from "./App.styles";

// TYPES
export type CartItemType = {
  id: number;
  category: string;
  description: string;
  image: string;
  price: number;
  title: string;
  amount: number;
}

const getProducts = async ():Promise<CartItemType[]> =>
  await (await fetch('https://fakestoreapi.com/products')).json();

const App = () => {
  const [cartOpen, setCartOpen] = useState(false);
  const [cartItem, setCartItem] = useState([] as CartItemType[])
  const { data, isLoading, error } = useQuery<CartItemType[]>('products', getProducts)
  

  const getTotalItems = (item: CartItemType[]) => item.reduce((ack:number,item)=>ack+item.amount,0);
  const handleAddToCart = (clickedItem: CartItemType) => {
    setCartItem(prev => {
      const isItemInCart = prev.find(item => item.id === clickedItem.id)
      if (isItemInCart) {
        return prev.map(item => 
          item.id===clickedItem.id?{...item,amount:item.amount+1}:item
        )
      }
      return [...prev,{...clickedItem,amount:1}]
    })
  };
  const handleRemoveFromCart = (id: number) => {
    setCartItem(prev => (
      prev.reduce((ack, item) => {
        if (item.id === id) {
          if (item.amount === 1) return ack;
          return [...ack,{...item,amount:item.amount-1}]
        } else {
          return [...ack,item]
        }
      },[] as CartItemType[])
    ))
  };

  if (isLoading) return <CircularProgress sx={{ position:'absolute',top:'40%',left:'50%',transform:'translate(-50%,-50%)'}} size='4rem'/>;
  if (error) return <div>Something went Wrong...</div>;
  
  return (
    <Wrapper>
      <Drawer anchor="right" open={cartOpen} onClose={()=>setCartOpen(false)}>
        <Cart setCartOpen={setCartOpen} cartItem={cartItem} addToCart={handleAddToCart} removeFromCart={handleRemoveFromCart}/>
      </Drawer>
      <StyledButton onClick={() => setCartOpen(true)}>
        <Badge badgeContent={getTotalItems(cartItem)} color='error'>
          <AddShoppingCart fontSize="large" />
        </Badge>
      </StyledButton>

      <Grid justifyContent="center"
   container spacing={3}>
        {data?.map(item => (
          <Grid item key={item.id} xs={12} sm={6} md={4} lg={3}>
            <Item item={item} handleAddToCart={handleAddToCart}/>
          </Grid>
        ))}
      </Grid>
    </Wrapper>
  );
}

export default App;
